@import url(https://fonts.googleapis.com/css2?family=Milonga&display=swap);
*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}@media (prefers-reduced-motion: reduce){*{-webkit-animation-duration:0.01ms !important;animation-duration:0.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:0.01ms !important;scroll-behavior:auto !important}}body{font-family:"Milonga", cursive;color:#bd8127;font-size:14px}

.menu-page{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100vw;height:100vh;background-image:url(/magic_game/static/media/menu-bg.8e5f9172.jpg)}.menu-page:after{content:'';position:absolute;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.6)}.menu-block{padding:20px;border-style:solid;border-image-width:7.5px;border-image-slice:15;border-image-repeat:round;border-image-source:url(/magic_game/static/media/border.6cff7f5e.png);z-index:1;color:#bd8127;background-color:rgba(0,0,0,0.85)}.menu-block__title{text-align:center}.menu-block__text{text-align:center}.menu{list-style:none;text-align:center;padding:0px;width:400px}.menu__item{padding-bottom:10px;cursor:pointer;transition:.3s}.menu__item:hover .menu__link{color:#e0a449;text-decoration:underline}.menu-character{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;cursor:pointer}.menu-character__img{width:20%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;border-radius:50%}.menu-character__info{text-align:left;padding-left:20px}.menu-character__name{margin:0 0 5px;transition:.3s}.menu-character__text{margin:0}

.game-page{width:100vw;height:100vh}

.location{position:relative;width:100vw;height:70vh;padding:20px;background-image:url("/magic_game/static/media/Diagon Alley.a70da261.jpg");background-position:bottom;background-size:cover}.location-top{display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start;-webkit-justify-content:space-between;justify-content:space-between}.location-bottom{width:calc(100% - 40px);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;position:absolute;bottom:0px}.location__title{text-align:center;font-size:20px;font-weight:700;margin-bottom:5px}

.villians{width:300px;padding:20px;border-style:solid;border-image-width:7.5px;border-image-slice:15;border-image-repeat:round;border-image-source:url(/magic_game/static/media/border.6cff7f5e.png);z-index:1;background-color:rgba(0,0,0,0.85)}.villian__img{float:left;width:50px;border-radius:50%;margin-right:10px}.villian__name{font-size:18px;margin:0}.villian__heals{margin-bottom:5px}

.characters{width:300px;padding:20px;border-style:solid;border-image-width:7.5px;border-image-slice:15;border-image-repeat:round;border-image-source:url(/magic_game/static/media/border.6cff7f5e.png);z-index:1;background-color:rgba(0,0,0,0.85)}.character{display:-webkit-flex;display:flex;-webkit-justify-content:start;justify-content:start;margin-bottom:10px}.character__img{width:50px;border-radius:50%;margin-right:10px}.character__name{font-size:18px;margin:0}.character-info{display:-webkit-flex;display:flex}.character-info p{margin-right:5px}

.location-block{padding:15px 40px;background:url(/magic_game/static/media/location-bg.376ec541.png);background-size:100% 100%;text-align:center;color:black}.location-block__name{font-size:20px;margin:0 0 5px}.location-block__influence{font-size:16px;margin:0}

.hero-block{width:100vw;height:30vh;background-color:#080503;padding:20px;display:-webkit-flex;display:flex}.hero-block-info{height:100%;padding-right:20px;-webkit-flex:1 1;flex:1 1}.hero-block-info__header{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}.hero-block-info__img{width:70px;border-radius:50%}.hero-block-info__name{text-align:left}.hero-block-info__text{margin-top:20px}.hero-block-cards{height:100%;-webkit-flex:4 1;flex:4 1;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;overflow-x:scroll;overflow-y:hidden}.hero-block-cards__item{-webkit-flex-shrink:0;flex-shrink:0;-webkit-flex-grow:0;flex-grow:0;margin-right:20px}.hero-block-cards::-webkit-scrollbar{height:20px}.hero-block-cards::-webkit-scrollbar-track{border-radius:10px}.hero-block-cards::-webkit-scrollbar-thumb{background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-position-x:right;background-position-y:center;position:absolute;border-radius:5px;box-shadow:0px -15px 5px -10px rgba(187,168,44,0.3) inset}.hero-block-cards::-webkit-scrollbar:hover{background:#333}.hero-block-cards::-webkit-scrollbar:active{background:#000000}

.card{border-style:solid;border-image-width:7.5px;border-image-slice:15;border-image-repeat:round;border-image-source:url(/magic_game/static/media/border.6cff7f5e.png);padding:4px;width:175px;height:220px;background:#be8227;color:#000}.card__img{height:90px;width:100%}.card__name{text-align:center;font-weight:700;margin:0;font-size:16px}.card__type{text-align:center;font-weight:700;margin:0}.card__type-spell{color:red}.card__text{padding:0 5px 5px 5px;text-align:center}

